.header {
  padding: 28px 0;
  position: relative;
  z-index: 1;
  box-shadow: 0px 1px 20px rgba(72, 3, 38, 0.07);
  background-color: #fff;
}

.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}

.buttons {
  display: flex;
  gap: 30px;
}

.button {
  width: 300px;
  height: 60px;
  border-radius: 30px;
  border: none;
  font-weight: 700;
  font-size: 18px;
  background-color: #00c78b;
  color: #fff;
}

.button:hover{  
  background-color: #06dd9d;
}

.button:disabled{  
  background-color: #bcd4cd;
}

@media screen and (max-width: 1024px) {
  .wrapper {
    flex-direction: column;
  }

  .buttons {
    gap: 20px;
  }

  .button {
    width: 220px;
    height: 45px;
    font-size: 14px;
  }
}

@media screen and (max-width: 480px) {
  .wrapper {
    gap: 20px;
  }

  .buttons {
    flex-direction: column;
  }
}
