@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  src: url(./fonts/open-sans-v29-latin_cyrillic-regular.woff2) format("woff2"),
    url(./fonts/open-sans-v29-latin_cyrillic-regular.woff) format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  src: url(./fonts/open-sans-v29-latin_cyrillic-700.woff2) format("woff2"),
    url(./fonts/open-sans-v29-latin_cyrillic-700.woff) format("woff");
}

@font-face {
  font-family: "BadScript";
  font-weight: 400;
  src: url(./fonts/BadScript-Regular.ttf) format("truetype");
}

html {
  box-sizing: border-box;
  min-height: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  background-color: #f1fffc;
  margin: 0;
}

button {
  cursor: pointer;
  padding: 0;
}

img,
svg {
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}
