.card {
  flex-grow: 1;
  padding: 32px 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "BadScript";
  text-align: center;
}

.wrapper {
  display: flex;
  align-items: center;
  text-align: center;
}

.image {
  margin: auto;
  position: relative;
  display: flex;
  box-shadow: 0px 0px 50px rgba(96, 4, 50, 0.23);
  font-size: 3px;
}

